body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

body{
  padding: 5px;
  margin: 5px;
	font-family: "Roboto", 'Helvetica Neue, Helvetica, Arial', sans-serif; 
  /* background: #ADD8E6;   */
  background: no-repeat center center fixed;
  background-image: url(/mandatory-advanced-js3/static/media/image2.b71de15a.jpg);
  background-size: cover;
}


/* Removing extra line from input field in material ui */
input:not([read-only]) {
	border-bottom: none!important;
  box-shadow: none!important;	
} 
label{
  margin:'4px';
}

.login__form{
  position: absolute; 
  left: 50%; 
  top: 50%;  
  -webkit-transform: translate(-50%, -50%);  
          transform: translate(-50%, -50%);  
  text-align:center;
}

.login__button{
  position: absolute; 
  left: 50%; 
  top: 80%;  
  -webkit-transform: translate(-50%, -50%);  
          transform: translate(-50%, -50%);  
  text-align:center;
  width: 270px;
}

.todo__form{
  position: absolute; 
  text-align:left;
  width:600px;
  border: 5px solid lightseagreen; 
  height: calc(50% + 200px);
  overflow-y: scroll;
}

.message{
  color:red;
  font-weight: bold;
}

.form{
  position: absolute; 
  left: 50%; 
  top: 30%;  
  -webkit-transform: translate(-50%, -50%);  
          transform: translate(-50%, -50%); 
  width:600px;
  height: 200px; 
}

::-webkit-scrollbar {
  display: none;
}
